const postData = async (url, data) => {
    const res = await fetch(url, {
        method: "POST",
        headers: {
            'Content-type': 'application/json'
        },
        body: data
    });

    return await res.json();
};

const getResource = async (url) => {
    const res = await fetch(url);

    if (!res.ok) {
        throw new Error(`Could not fetch ${url}, status: ${res.status}`); // Выкидываем ошибку в ручную, чтобы сработал блок кода catch(не работает, если, к примеру, есть ошибка в url)
    }

    return await res.json();
}

export {postData};
export {getResource};